import React from 'react'
import Grid from 'react-bootstrap/lib/Grid'
import Panel from 'react-bootstrap/lib/Panel'
import Row from 'react-bootstrap/lib/Row'
import {injectIntl} from 'react-intl'

import {setTitle} from '../lib/utils'
//import PaymentTable from './PaymentTable'
class OTCTradebuy extends React.Component {
render() {
    const {formatMessage} = this.props.intl
    setTitle('OTCTrade')
    return (
      <Grid>
        <Row> <div  >
        <a style={{backgroundColor: '#a34ea7',color: 'white',letterspacing:'0.05em',padding: '4px 12px 4px',borderRadius:'60px'}} href="otctradebuy"> {formatMessage({id: 'I want Buy'})} </a> &nbsp; &nbsp;
          <a style={{backgroundColor: '#a34ea7',color: 'white',letterspacing:'0.05em',padding: '4px 12px 4px',borderRadius:'60px'}} href="otctradesell"> {formatMessage({id: 'I want Sell'})}</a>  &nbsp; &nbsp;
          <a style={{backgroundColor: '#a34ea7',color: 'white',letterspacing:'0.05em',padding: '4px 12px 4px',borderRadius:'60px'}} href="otctrade"> {formatMessage({id: 'My order'})}</a>  &nbsp;
          </div><br></br>
          <Panel header={formatMessage({id: 'newest list'})}>
         
          
          </Panel>
         
         
        </Row>
      </Grid>
    )
  }
}

export default injectIntl(OTCTradebuy)



